'use strict'

let HOST = '//39.100.43.169:8091';
let APIPATH = '/api/v1/app';
let PagePATH = '/test/web';
let Protocol = location.protocol;

if(1 || location.host=='browser.xsed.com.cn' || Protocol=='file:'){
    HOST = '//browser.xsed.com.cn';
    PagePATH = '/pro/web';
    if(Protocol=='file:') {
        Protocol='http:'
    }
}
let HostUrl = Protocol+HOST;

// 做请求节流；
const requestUrlObj = {};
function deleteRequestUrl(url) {
    delete requestUrlObj[url];
}

export function getLocalHost() {
    return Protocol+HOST;
}
export function getLocalUrl(type='api') {
    if(type=='api'){
        return Protocol+HOST+APIPATH;
    }else if(type=='page'){
        return Protocol+HOST+PagePATH;
    }
}
//定义api
export function getApi(name,id,type) {
    const APIS = {
        'news':         `/hot/${id}/${type}`,            //新闻详情页
        'wx'    :       `/article/wx/${id}/${type}`,     //微信内容详情页
        'video' :       `/video/${id}/${type}`,          //视频内容详情页
        'smallVideo':   `/small/video/${id}/${type}`,    //小视频内容详情页
        'task':         `/activity/list`,                //任务列表
        'taskSignBox':  `/signbox/signBox`,              //任务 签到和宝箱
        'taskSignCoin': `/signbox/sign`,                 //任务 签到 点击
        'taskOpenBox':  `/signbox/openBox`,              //任务 宝箱 点击
    }

    if(name){
        return HostUrl+APIPATH+APIS[name]
    }else{
        return {
            host: HOST,
            apis:APIS
        }
    }
}

